import React, { useState } from 'react';
import { TextField, Button, Container, Typography, Checkbox, FormControlLabel, Link, Divider } from '@mui/material';
import { Google as GoogleIcon, Facebook as FacebookIcon } from '@mui/icons-material';
import Stack from '@mui/material/Stack';
import axios from 'axios';


function Login() {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');

    const handleLogin = async () => {
        try {
            const response = await axios.post('/api/users/login', {
                userEmail: email,
                userPassword: password,
            });
            alert('로그인 성공');
            // 로그인 성공 후 리다이렉트 처리
            window.location.href = '/';
        } catch (error) {
            setError('이메일 또는 비밀번호가 잘못되었습니다.');
        }
    };

    return (

        <Container maxWidth="xs" style={{ marginTop: '50px' }}>
            <Stack direction="column" alignItems="center" spacing={2}>
                <Typography variant="h4" component="h1">
                    Sign in
                </Typography>
            </Stack>

            {/* 이메일 및 비밀번호 필드 */}
            <TextField
                label="Email"
                fullWidth
                margin="normal"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
            />
            <Stack direction="row" justifyContent="space-between" alignItems="center">
                <TextField
                    label="Password"
                    type="password"
                    fullWidth
                    margin="normal"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                />
                <Link href="#" underline="none" variant="body2">
                    Forgot your password?
                </Link>
            </Stack>

            {/* 기억하기 체크박스 */}
            <FormControlLabel
                control={<Checkbox color="primary" />}
                label="Remember me"
            />

            {/* 로그인 버튼 */}
            <Button
                variant="contained"
                fullWidth
                style={{ marginTop: '20px', backgroundColor: '#000000' }}
                onClick={handleLogin}
            >
                Sign in
            </Button>

            {/* 에러 메시지 */}
            {error && (
                <Typography color="error" style={{ marginTop: '10px' }}>
                    {error}
                </Typography>
            )}

            {/* 회원가입 링크 */}
            <Typography variant="body2" align="center" style={{ marginTop: '10px' }}>
                Don't have an account?{' '}
                <Link href="/signup" underline="none">
                    Sign up
                </Link>
            </Typography>

            {/* 구분선 */}
            <Divider style={{ marginTop: '20px', marginBottom: '10px' }}>or</Divider>

            {/* 소셜 로그인 버튼 */}
            <Stack direction="column" spacing={2}>
                <Button
                    variant="outlined"
                    startIcon={<GoogleIcon />}
                    fullWidth
                >
                    Sign in with Google
                </Button>
                <Button
                    variant="outlined"
                    startIcon={<FacebookIcon />}
                    fullWidth
                >
                    Sign in with Facebook
                </Button>
            </Stack>
        </Container>
    );
}

export default Login;
